const obj = require('./modu.js');


document.getElementById('addButton').addEventListener('click' , () => {

    let first = parseInt(document.getElementById('first').value);
    let second = parseInt(document.getElementById('second').value);

    const result_id = ['result_01' , 'result_02' , 'result_03' , 'result_04'];
    const result_txt = [`더하기 : ` , `빼기 : ` , `곱하기 : ` , `나누기 : `];
    const fun = [obj.add , obj.minus , obj.multiplied , obj.divide];

    if(isNaN(first) || isNaN(second)) 
    {
        for(let i = 0 ; i < result_id.length ;i++)
        {
            document.getElementById(result_id[i]).innerText = `둘다 제대로 된 값을 입력해 주세요`;
        }

    }

    else 
    {
        
        for(let i = 0 ; i < result_id.length ; i++)
        {
            let answer = fun[i](first,second);
            document.getElementById(result_id[i]).innerText = result_txt[i] + answer;
        }

       
    }


});
